.movie__list{display: flex; align-self: flex-start;  width: 50%; padding: 20px; box-sizing: border-box;}
.movie__list .card{background: #fff; box-shadow: 5px 5px 20px rgba(0,0,0,.2); border-radius: 5px;}
.movie__list .imgBox {width: 30%; display: inline-block; margin-top: -20px;vertical-align: top; padding: 10px; box-sizing: border-box;}
.movie__list .imgBox img{max-width: 100%;  box-shadow: -5px 5px 15px rgba(12, 12, 12, 0.4);}
.movie__list .txtBox{display: inline-block; width: 70%; vertical-align: top; padding:  0 10px; box-sizing: border-box;}
.movie__list .txtBox .movie__title{font-size: 14px;}
.movie__list .txtBox .movie__summary{max-height: 47px; padding-right: 10px; margin: 5px 0; overflow: hidden;  font-weight: 300; text-overflow: ellipsis; display: -webkit-box; -webkit-box-orient: vertical; -webkit-line-clamp: 3; font-size: 11px; color: #666}
.movie__list .txtBox .info{font-size: 12px; color: #999; word-break: break-all;}
.movie__list .genres {text-align: left; margin: 0; padding: 0; font-size: 10px; border-top: 1px dotted #eee; padding: 15px 20px; box-sizing: border-box; color: #999}
.movie__list .genres li{text-decoration: none; list-style: none; display: inline-block;}
.movie__list .genres li + li::before{content: "/"; margin: 0 5px; color: #ccc}